import React, { useContext, useState, useEffect } from 'react';
import { StateStore } from '../store';
import './Registration.scss';
import { register, unregister } from '../Actions/UserActions';
import Loading from './Loading';
import csc from 'country-state-city';

export const options = [
  "Shia Ithna-Ansari",
  "University of Islamic Sciences, Karachi",
  "Islamic Society of North America",
  "Muslim World League",
  "Umm Al-Qura University, Makkah",
  "Egyptian General Authority of Survey",
  "Institute of Geophysics, University of Tehran",
  "Gulf Region",
  "Kuwait",
  "Qatar",
  "Majlis Ugama Islam Singapura, Singapore",
  "Union Organization islamic de France",
  "Diyanet İşleri Başkanlığı, Turkey",
  "Spiritual Administration of Muslims of Russia"]

export default function Registration() {

  const { dispatch } = useContext(StateStore);
  const countries = csc.getAllCountries();
  const [cities, setCities] = useState(null);
  const [states, setStates] = useState(null);

  const [formState, setFormState] = useState({ calcMethod: 1, theme: 'dark' });
  // const [loading, setLoading] = useState(false);
  // const [error, setError] = useState(null);

  const formUpdated = ({ target }) => {
    if (target.name == "country") {
      setFormState({
        ...formState,
        country: target.options[target.selectedIndex].innerHTML,
      });
      setStates(csc.getStatesOfCountry(target.value));
    } else if (target.name == "state") {
      setFormState({
        ...formState,
        state: target.options[target.selectedIndex].innerHTML,
      });
      setCities(csc.getCitiesOfState(target.value));
    } else if (target.name == "city") {
      setFormState({
        ...formState,
        city: target.options[target.selectedIndex].innerHTML,
      });
    } else {
      setFormState({ ...formState, [target.name]: target.value });
    }
  }

  return (
    <div className="register-container">
      <div className="card register-card">
        <div className="heading">
          Prayer Journal<br></br>
        </div>
        <p>Before we begin, select a Prayer times calculation method and your location</p>

        <select onChange={formUpdated} value={formState.calcMethod} name="calcMethod">
          {options.map((option, i) => <option value={i}>{option}</option>)}
        </select>

        <select name="country" id="country" value={null} onChange={formUpdated}>
          <option>Select Country</option>
          {countries.map((option, i) => <option value={option.id}>{option.name}</option>)}
        </select>

        {states ?
          (<select name="state" id="state" onChange={formUpdated}>
            <option>Select State</option>
            {states.map((option, i) => <option value={option.id}>{option.name}</option>)}
          </select>)
          : null}

        {cities ?
          (<select name="city" id="city" onChange={formUpdated}>
            <option>Select City</option>
            {cities.map((option, i) => <option value={option.id}>{option.name}</option>)}
          </select>)
          : null}

        {(formState.city) ? <button className="btn primary clickable" onClick={() => register(dispatch, formState)}>Save</button> : null}
      </div>
    </div>
  )
}
